.copyright-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;

  @media (min-width: @tablet-width) {
    width: min-content;
  }
}

.copyright-container__logo {
  height: 34px;
  display: flex;
  align-items: center;
  box-sizing: content-box;
  color: #666666;

  @media (min-width: @tablet-width) {
    padding-right: 23px;
  }

  &--img:hover {
    color: @brand-color;
  }

  &:active {
    opacity: 0.3;
  }

  &--img:active {
    color: @brand-color;
  }
}



